import DexieTable from "components/CodingjobManager/subcomponents/DexieTable";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

import { Header, Button } from "semantic-ui-react";

const tableColumns = [
  { name: "title" },
  { name: "set" },
  { name: "progress", f: (row) => `${row.n_coded || 0} / ${row.units.length}` },
  { name: "last_modified" },
  { name: "id", hide: true },
];

const LocalJobTable = () => {
  const history = useHistory();
  const [jobKey, setJobKey] = useState(null);

  const setJobUrlQuery = async () => {
    // set task.url as url query to open job in annotator
    history.push("/annotator?id=" + jobKey.id);
  };

  return (
    <>
      <Header textAlign="center" style={{ background: "#1B1C1D", color: "white" }}>
        Your codingjobs
      </Header>
      <Button fluid primary disabled={!jobKey} onClick={setJobUrlQuery}>
        Open selected codingjob
      </Button>
      <br />
      <br />
      <DexieTable
        table={"localJobs"}
        columns={tableColumns}
        setSelected={setJobKey}
        searchOn="title"
      />
    </>
  );
};

export default LocalJobTable;
